export default {
	data() {
		return {
			eyeImg1:true,
			eyeImg2:true,
			eyeImg3:true,
			eyeImg4:false,
			biId:'',
			level:2,
			comment:'',
			tags:'',
			appoint:'',
			authKey:'',
			tagsList:'',
			activeIndex:'',
			anonymous:0,
			selectedA:false,
			showTag:''
		};
	},
	onLoad(e) {
		this.authKey = uni.getStorageSync('authKey');
		this.biId = e.id
		this.detail();
	},
	methods:{
		levela(e){
			this.level = e
			if(e == 2){
				this.eyeImg1 = true
	            this.eyeImg2 = true
	            this.eyeImg3 = true
			}else if(e == 1){
				this.eyeImg1 = false
				this.eyeImg2 = false
				this.eyeImg3 = true
			}else{
				this.eyeImg1 = false
				this.eyeImg2 = true
				this.eyeImg3 = false
			}
		},
		commentInput: function(e) {
			if (this.comment.indexOf(e.target.value) < 0)
				this.comment = e.target.value;
		},
		bindEye() {
			this.eyeImg4 = !this.eyeImg4;
			if(this.eyeImg4 == true){
				this.anonymous = 1
			}else{
				this.anonymous = 0
			}
			console.log(this.anonymous)
		},
		remark(e,index){
			this.eyeImg5 = true
			if (this.showTag.indexOf(e.remark) < 0){
				this.showTag += e.remark + ','
				this.tags.push(e.id)
			}
		},
		// 预约信息详情
		detail(){
			let that = this
			uni.request({
				url: this.http+'appointment/detail/' + this.biId,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
				},
				success: res => {
					console.log(res)
					if (res.data.code == 200) {
						let appoint = res.data.data
						appoint.product.cover = that.static + appoint.product.cover
						that.appoint = appoint
					}
				},
			});
			// 获取标签
			uni.request({
				url: this.http+'appointment/virtues',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
				},
				success: res => {
					console.log(res)
					if (res.data.code == 200) {
						this.tagsList = res.data.data
					}
				},
			});
		},
		// 提交评价
		submit(){
			uni.request({
				url: this.http+'appointment/evaluate',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {
					appointment_id:this.biId,
					level:this.level,
					comment:this.comment,
					tags:this.tags,
					anonymous:this.anonymous
				},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
			             uni.showToast({
			             	title:'评价成功',
			             	icon:'none',
			             	duration:1500
			             })
						 uni.reLaunch({
						 	url: '../../order/order?tabIndex=' + 4
						 });
					}
				},
			});
		}
	}
}
